import clipboard from 'clipboard-polyfill'
import { t } from 'i18next'
import { message } from 'antd'

export default url => {
  return clipboard.writeText(url)
    .then(val => {
      message.success(`${t('copy.copied')}: ${url.replace(/^(.{20}).*(.{20})$/g, '$1...$2')}`, 0.7)
    }, () => { message.error(t('copy.error')) })
}



